import logoBlack from './assets/icons/logo_black.png'
import title from './assets/icons/title.svg'


export function Playground({ children }) {
  return (
    <div id="playground">
      {children}
    </div>
  );
}


export function Heading () {
  return <div id='heading'>
    <div id='avatar'>
      <img src={logoBlack} alt='' id='avatar-logo'/>
    </div>
    <div>
      <img src={title} alt='' id='title'/>
    </div>
    
  </div>
}


export function FullGrid({ children }) {
  return (
    <div id="full-grid">
      {children}
    </div>
  );
}


export function BeatGrid({ children }) {
  return (
    <div id="beat-grid">
      {children}
    </div>
  );
}


export function BeatColumn({ children, isPlayed }) {
  return (
    <div className={isPlayed ? 'beat-column-played' : 'beat-column'} >
      {children}
    </div>
  );
}


export function ButtonColumn({ children }) {
  return <div className='button-column'>
    {children}
  </div>
}